import React, { useState, useEffect } from "react";
import Swal from "sweetalert2";

import { parts } from "../mock_data/parts";
import ProductSelect from "./ProductSelect";

function Build() {
  const [products, setProducts] = useState([]);
  const [mobo, setMobo] = useState();
  const [cpu, setCpu] = useState();

  useEffect(() => {
    if (products.length === 0) fetchProducts();

    console.log(cpu, mobo);
    if (cpu !== undefined && mobo !== undefined) {
      if (cpu !== mobo) {
        Swal.fire({
          icon: "error",
          title: "Incompatibility detected",
          text: "Your cpu and motherboard are not compatible",
        });
      }
    }
  }, [cpu, mobo]);

  const fetchProducts = () => {
    console.log(parts);
    setProducts(parts);
  };

  return (
    <React.Fragment>
      <table className="table">
        <thead>
          <tr>
            <th scope="col">Type</th>
            <th scope="col">Name</th>
            <th scope="col">Price</th>
            <th scope="col">Availability</th>
            <th scope="col">Select</th>
          </tr>
        </thead>
        <tbody>
          <ProductSelect type="cpu" products={products} setCpu={setCpu} />
          <ProductSelect
            type="motherboard"
            products={products}
            setMobo={setMobo}
          />
        </tbody>
      </table>
    </React.Fragment>
  );
}

export default Build;
