import React, { useState } from "react";

import { store } from "../mock_data/store";

function ProductSelect({ products, type, setCpu, setMobo }) {
  const [activeProduct, setActiveProduct] = useState();
  const allStores = store;

  const renderSelect = () => {
    return products
      .filter((p) => p.type === type)
      .map((p, i) => (
        <option key={i} value={p.name}>
          {p.name}
        </option>
      ));
  };

  const renderAvailability = () => {
    if (activeProduct === undefined) return;
    const stores = [];
    activeProduct.store.forEach((s) => {
      stores.push(
        allStores.find((element) => {
          return element.id === s;
        })
      );
    });

    return stores.map((s, i) => <img key={i} src={s.image} height="40x" />);
  };

  const handleChange = (e) => {
    const value = e.target.value;
    const product = products.find((element) => {
      return element.name === value;
    });

    setActiveProduct(product);
    if (product.type === "cpu") {
      setCpu(product.brand);
    } else if (product.type === "motherboard") {
      setMobo(product.brand);
    }
  };

  return (
    <React.Fragment>
      <tr>
        <th scope="row">{type + ": "}</th>
        <td>{activeProduct !== undefined ? activeProduct.name : "/"}</td>
        <td>{activeProduct !== undefined ? activeProduct.price : "/"}</td>
        <td>{renderAvailability()}</td>
        <td>
          <select
            className=""
            style={{ width: "100%" }}
            onChange={handleChange}
          >
            <option></option>
            {renderSelect()}
          </select>
        </td>
      </tr>
    </React.Fragment>
  );
}

export default ProductSelect;
